import SignupForm from "components/SignupForm";
import React, { useEffect, useState } from "react";
import Button from "react-bootstrap/Button";
import ProgressBar from "react-bootstrap/ProgressBar";
import Row from "react-bootstrap/Row";
import Spinner from "react-bootstrap/Spinner";
import { getQuestions, Question } from "./api";
import Cross from "./images/cross";
import Tick from "./images/tick";
import {
    AnswerCol,
    OptionCont,
    OptionImage,
    QuestionImage,
    QuestionText,
    ResultItem,
    Results,
    ReviewButton,
} from "./styled";
import { randomizeQuestions } from "./utils";

interface Answered {
    questionId: number;
    answerId: number;
    isCorrect: boolean;
}

const Quiz = ({
    showSignupForm = false,
    isPage,
}: {
    showSignupForm?: boolean;
    isPage: boolean;
}) => {
    const [questions, setQuestions] = useState<Question[]>([]);
    const [questionIndex, setQuestionIndex] = useState<number>(0);
    const [answeredQuestions, setAnsweredQuestions] = useState<Answered[]>([]);
    const [ticked, setTicked] = useState<number | null>(null);
    const [crossed, setCrossed] = useState<number | null>(null);

    const question = questions[questionIndex];

    useEffect(() => {
        getQuestions(isPage).then((result) => setQuestions(result));
    }, [isPage]);

    const nextQuestion = (answerId: number) => {
        if (!question || ticked !== null || crossed !== null) return;
        const correctAnswer = question.answers.find(
            (answer) => answer.isCorrect
        );
        const isCorrect = correctAnswer ? correctAnswer.id === answerId : false;
        const answered = {
            questionId: question.id,
            answerId,
            isCorrect,
        };
        setAnsweredQuestions([
            answered,
            ...answeredQuestions.filter(
                (answer) => answer.questionId !== question.id
            ),
        ]);
        setTimeout(() => {
            setQuestionIndex(questionIndex + 1);
            setTicked(null);
            setCrossed(null);
        }, 2000);
        isCorrect ? setTicked(answerId) : setCrossed(answerId);
    };

    const resetQuiz = () => {
        setQuestions(randomizeQuestions(questions));
        setQuestionIndex(0);
        setAnsweredQuestions([]);
    };

    const progress =
        questions.length > 0
            ? ((questionIndex + 1) / questions.length) * 100
            : 0;

    let content;

    if (questions.length > 0 && questionIndex === questions.length) {
        content = (
            <Results isPage={isPage}>
                <ResultItem>
                    Total correct:{" "}
                    {
                        answeredQuestions.filter(
                            (answered) => answered.isCorrect
                        ).length
                    }
                </ResultItem>
                {showSignupForm && (
                    <ResultItem>
                        <SignupForm />
                    </ResultItem>
                )}
                {isPage && (
                    <ResultItem>
                        <ReviewButton variant="link">
                            Review all questions
                        </ReviewButton>
                    </ResultItem>
                )}
                <ResultItem>
                    <Button onClick={resetQuiz} variant="link">
                        Try again
                    </Button>
                </ResultItem>
            </Results>
        );
    } else if (!question) {
        content = (
            <Row>
                <Spinner animation="border" />
            </Row>
        );
    } else {
        content = (
            <React.Fragment>
                <Row>
                    <ProgressBar
                        style={{
                            width: "100%",
                            height: "30px",
                        }}
                        now={progress}
                        label={`${questionIndex + 1} / ${questions.length}`}
                    />
                </Row>

                <Row>
                    <QuestionText>{question.question}</QuestionText>
                    {question.img && <QuestionImage src={question.img} />}
                </Row>
                <Row>
                    {question.answers.map((answer) => {
                        const isTicked = answer.id === ticked;
                        const isCrossed = answer.id === crossed;
                        return (
                            <AnswerCol
                                lg="6"
                                key={`${question.id}-${answer.id}`}
                            >
                                <OptionCont
                                    onClick={() => nextQuestion(answer.id)}
                                >
                                    {answer.img && (
                                        <OptionImage
                                            isTransparent={
                                                isTicked || isCrossed
                                            }
                                            src={answer.img}
                                        />
                                    )}
                                    {answer.answer && (
                                        <div> {answer.answer} </div>
                                    )}

                                    {isTicked && <Tick />}
                                    {isCrossed && <Cross />}
                                </OptionCont>
                            </AnswerCol>
                        );
                    })}
                </Row>
            </React.Fragment>
        );
    }
    return content;
};

export default Quiz;
